import { useQuery } from '@tanstack/react-query';
import { getAllBrandsService } from '../services/brands';

const useBrands = () => {
  const { data, isLoading } = useQuery({
    queryKey: ['todos'],
    queryFn: getAllBrandsService,
  });

  return {
    isLoading,
    data: data?.data || [],
  };
};

export default useBrands;
